<template>
  <div>
    <div class="back pl-3">
      <b-button @click="$emit('back', 'payment-component')" id="btn-back">
        <b-icon icon="arrow-left-short" variant="light" scale="2"></b-icon>
      </b-button>
    </div>
    <b-card title="Isi Nominal Donasi" title-tag="h5" class tag="article">
      <b-input-group size="md" right prepend="Rp" class="py-2">
        <b-form-input
          autofocus
          v-model="form.nominal"
          :state="validationShow"
        ></b-form-input>
      </b-input-group>
      <hr />
      <b-media vertical-align="top">
        <template v-slot:aside>
          <b-img
            left
            class="border p-1"
            :src="require(`@/assets/payment/${payment.logo}`)"
            blank-color="#ccc"
            width="64"
            height="40"
            alt="placeholder"
          ></b-img>
        </template>
        <h6 class="mt-2 align-middle">
          {{ payment.nama }}
          <span
            @click="$emit('ganti-pembayaran')"
            class="float-right bg-primary p-1 px-2 rounded-pill text-white ganti"
          >
            Ganti
            <b-icon icon="arrow-down-up" scale="1.3" class="ml-1"></b-icon>
          </span>
        </h6>
      </b-media>
      <hr />
      <p class="text-center">Lengkapi data dibawah ini</p>
      <b-form @submit.prevent="submit" id="form" validated>
        <b-form-group>
          <b-form-input
            v-model="form.nama"
            type="text"
            required
            placeholder="Nama Lengkap"
          ></b-form-input>
        </b-form-group>
        <b-form-group>
          <b-form-input
            v-model="form.email"
            type="email"
            required
            placeholder="Masukan Email Anda"
          ></b-form-input>
        </b-form-group>
        <b-form-group class="mb-0">
          <p class="d-flex justify-content-between">
            Sembunyikan nama saya (donasi anonim)
            <span class>
              <b-form-checkbox
                name="check-button"
                value="true"
                v-model="form.anonim"
                switch
              ></b-form-checkbox>
            </span>
          </p>
        </b-form-group>
        <b-form-group
          class="mb-0"
          label="Tulis doa dan dukungan (opsional)"
          label-for="textarea-formatter"
        >
          <b-form-textarea
            rows="3"
            required
            id="textarea-formatter"
            v-model="form.dukungan"
            trim
            placeholder="beri doa dan dukunganmu disini"
          ></b-form-textarea>
        </b-form-group>
        <b-navbar id="btn-container" fixed="bottom">
          <b-button
            block
            type="submit"
            variant="danger"
            center
            id="btn-pembayaran"
            :disabled="!validationShow"
          >
            <strong>LANJUTKAN PEMBAYARAN</strong>
          </b-button>
        </b-navbar>
      </b-form>
    </b-card>
    <b-card class="pb-5">
      <small class="text-muted clearfix">
        Donasi Pengalangan Dana
        <span class="float-right">Rp {{ form.nominal | format }}</span>
      </small>
      <small class="text-muted clearfix">
        Payment Method
        <span class="float-right">{{ payment.nama }}</span>
      </small>
      <h5 class="mt-2 clearfix">
        <strong>
          Total
          <span class="float-right">Rp {{ form.nominal | format }}</span>
        </strong>
      </h5>
    </b-card>

    <b-toast
      id="my-toast"
      :variant="toast.variant"
      solid
      auto-hide-delay="1000"
    >
      <template v-slot:toast-title>
        <div class="d-flex flex-grow-1 align-items-baseline">
          <b-icon
            icon="check2-circle"
            :variant="toast.variant"
            scale="1.8"
            class="mr-2"
          ></b-icon>
          <strong class="mr-auto">{{ toast.message }}</strong>
          <small class="text-muted mr-2">02 seconds ago</small>
        </div>
      </template>
      <div class="text-center">
        <b-spinner
          small
          variant="primary"
          type="grow"
          label="Spinning"
        ></b-spinner>
        <span class="mx-2">Menyiapkan Halaman..</span>
        <b-spinner
          small
          variant="primary"
          type="grow"
          label="Spinning"
        ></b-spinner>
      </div>
    </b-toast>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "Final",
  props: {
    payment: Object,
    nominalProps: Number
  },
  data() {
    return {
      validationShow: true,
      form: {
        nama: "",
        nominal: this.nominalProps,
        payment: this.payment.nama,
        email: "",
        anonim: "false",
        dukungan: ""
      },
      toast: {
        variant: null,
        message: null
      }
    };
  },
  updated() {
    this.validationShow = this.validation;
  },
  filters: {
    format: function(val) {
      return val.toLocaleString();
    }
  },
  methods: {
    submit: function() {
      let form = document.getElementById("form");
      const fd = new FormData(form);
      fd.append("nama", this.form.nama);
      fd.append("payment", this.form.payment);
      fd.append("nominal", this.form.nominal);
      fd.append("email", this.form.email);
      fd.append("anonim", this.form.anonim);
      fd.append("dukungan", this.form.dukungan);
      axios
        .post(
          "https://my-json-server.typicode.com/SyafiqGhiyats/database/donasi",
          fd
        )
        .then(() => {
          this.showToast("primary", " Ditambahkan");
          setTimeout(() => {
            this.$router.push({ path: "/" });
          }, 900);
        })
        .catch(() => {
          this.showToast("danger", "Gagal Ditambahkan");
          this.form = {
            nama: "",
            nominal: this.nominalProps,
            payment: this.payment.nama,
            email: "",
            anonim: "false",
            dukungan: ""
          };
        });
    },
    showToast(variant, message) {
      this.toast.variant = variant;
      this.toast.message = message;
      this.$bvToast.show("my-toast");
    }
  },
  computed: {
    validation() {
      return (
        this.form.nominal.length > 4 &&
        !isNaN(this.form.nominal) &&
        this.form.nominal.length < 9 &&
        parseInt(this.form.nominal) / 10000 !== 0 &&
        Number.isInteger(parseInt(this.form.nominal) / 10000)
      );
    }
  }
};
</script>

<style>
.ganti {
  cursor: pointer;
}
</style>
