import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Donasi from "../views/Donasi.vue";
Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home
  },
  {
    path: "/donasi",
    name: "Donasi",
    component: Donasi
  }
];
const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
