<template>
  <div>
    <b-card
      title="Cegah Penyebaran Corona Di Indonesia"
      img-src="../assets/corona.jpg"
      img-alt="Image"
      img-top
      tag="article"
      class="shadow"
    >
      <p>
        <strong class="text-primary">Rp {{ formating(jumlahDonasi) }}</strong
        >&nbsp; Terkumpul dari Rp 1000.000
      </p>
      <b-progress
        :value="jumlahDonasi"
        max="1000000"
        variant="info"
        :animated="animated"
        :striped="striped"
        class="mt-2"
      ></b-progress>
      <p>
        <span class="float-left">
          <strong>{{ donasi.length }}</strong> Donasi
        </span>
        <span class="float-right"> <strong>36</strong> Hari lagi </span>
      </p>
      <b-button variant="danger" to="/donasi" class="mt-3 p-2" id="btn-donasi">
        <strong>Donasi Sekarang!</strong>
      </b-button>
    </b-card>
    <b-card title="Penggalang Dana" tag="article" class="shadow">
      <b-media class="shadow mb-4 pl-3 pt-3 pr-3">
        <template v-slot:aside>
          <b-avatar src variant="primary"></b-avatar>
        </template>
        <h5 class="text-primary mt-0">Jainudin sh</h5>
        <small class="text-muted">Identitas Terverivikasi</small>
        <b-alert id="desk" show variant="info"
          >Mengalangkan dana untuk disumbangkan kepada masyarakat kecil</b-alert
        >
      </b-media>
      <b-media class="shadow p-3" right-align vertical-align="center">
        <template v-slot:aside>
          <b-img
            src="../assets/validate.svg"
            blank-color="#ccc"
            width="80"
            alt="placeholder"
          ></b-img>
        </template>
        <h5 class="mt-0 mb-1">Informasi Galang Dana Tervalidasi</h5>
        <p class="mb-0">
          Tim akubisa telah memeriksa kebenaran kondisi dan dokumen pendukung
        </p>
      </b-media>
    </b-card>
    <b-card title="Kabar Terbaru" tag="article" class="shadow pb-4">
      <b-img
        src="../assets/news.jpg"
        blank-color="#ccc"
        width="200"
        center
        alt="placeholder"
      ></b-img>
      <p class="text-muted text-center">
        Belum ada kabar terbaru penggalangan dana ini
      </p>
      <h3 class="mb-3">Donatur({{ donasi.length }})</h3>
      <b-media
        id="list-donasi"
        class="mb-4 pl-3 pt-3 pr-3"
        v-for="don in donasi"
        :key="don.id"
      >
        <template v-slot:aside>
          <b-avatar src variant="success"></b-avatar>
        </template>
        <h6 class="text-primary mt-0">
          <strong v-if="don.anonim === 'false'">{{ don.nama }}</strong>
          <strong v-else>Anonim</strong>
        </h6>
        <p>
          Donasi
          <strong>{{ formating(don.nominal) }}</strong>
          <span class="float-right text-muted">
            <!-- <b-img
              blank
              blank-color="#000"
              width="64"
              height="40"
              alt="placeholder"
              :src="require(`@/assets/payment/${don.payment.toLowerCase()}.png`)"
            />-->
            <small>{{ don.payment }}</small>
          </span>
          <small class="text-muted">
            <br />
            <span>{{ don.dukungan }}</span>
          </small>
        </p>
      </b-media>
    </b-card>
    <b-navbar id="btn-container" fixed="bottom" v-if="showBtn">
      <b-button block variant="danger" to="/donasi" center id="btn-pembayaran">
        <strong>DONASI SEKARANG</strong>
      </b-button>
    </b-navbar>
  </div>
</template>

<script>
export default {
  name: "Home",
  props: {
    donasi: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      striped: true,
      animated: true,
      showBtn: false,
      arrDonasi: [],
      totalDonasi: null
    };
  },
  mounted() {
    window.addEventListener("scroll", () => {
      if (window.scrollY > 900) {
        this.showBtn = true;
      } else {
        this.showBtn = false;
      }
    });
  },
  updated() {
    this.addNominal();
  },
  methods: {
    formating(n) {
      return Intl.NumberFormat().format(parseInt(n));
    },
    addNominal() {
      if (this.arrDonasi.length == 0) {
        for (let key in this.donasi) {
          this.arrDonasi.push(parseInt(this.donasi[key].nominal));
        }
      }
    }
  },
  computed: {
    jumlahDonasi() {
      const sum = this.arrDonasi.reduce(function(a, b) {
        return a + b;
      }, 0);
      return sum;
    }
  }
};
</script>

<style lang="scss" scoped>
#desk {
  position: relative;
  left: -20px;
}
#list-donasi {
  margin: -10px 0;
  background: rgb(238, 238, 238);
  position: relative;
}
#btn-donasi {
  width: 100% !important;
}
</style>
