<template>
  <home-view :donasi="donasi"></home-view>
</template>

<script>
import Donasi from "@/components/Home.vue";
import axios from "axios";
const baseUrl =
  "https://my-json-server.typicode.com/SyafiqGhiyats/database/donasi";
export default {
  name: "Home",
  components: {
    "home-view": Donasi
  },
  data() {
    return {
      striped: true,
      animated: true,
      donasi: []
    };
  },
  methods: {
    formating(n) {
      return Intl.NumberFormat().format(parseInt(n));
    },
    getDonasi() {
      axios
        .get(baseUrl)
        .then(res => (this.donasi = res.data))
        .catch(err => console.log(err));
    }
  },
  created() {
    this.getDonasi();
  }
};
</script>

