<template>
  <div>
    <div class="back pl-3">
      <b-button @click="$emit('back', 'donasi-component')" id="btn-back">
        <b-icon icon="arrow-left-short" variant="light" scale="2"></b-icon>
      </b-button>
    </div>
    <b-card no-body>
      <template v-slot:header>
        <h6 class="m-0 p-0">
          <strong>Pembayaran Kilat</strong>
        </h6>
      </template>
      <b-list-group flush>
        <b-list-group-item
          class="list-item"
          @click="setPayment(kilat.nama, kilat.logo)"
          v-for="kilat in payment.kilat"
          :key="kilat.nama"
        >
          <b-media vertical-align="end">
            <template v-slot:aside>
              <b-img
                left
                :src="require(`@/assets/payment/${kilat.logo}`)"
                blank-color="#ccc"
                width="64"
              ></b-img>
            </template>
            <h6 class="mt-1 align-middle">
              {{ kilat.nama }}
              <span v-if="kilat.nama === paymentSelected.nama">
                <b-icon-check
                  variant="primary"
                  class="float-right"
                  scale="2"
                ></b-icon-check>
              </span>
            </h6>
          </b-media>
        </b-list-group-item>
      </b-list-group>
    </b-card>

    <b-card no-body>
      <template v-slot:header>
        <h6 class="m-0 p-0">
          <strong>Pembayaran Via Bank</strong>
        </h6>
      </template>
      <b-list-group flush>
        <b-list-group-item
          class="list-item"
          @click="setPayment(bank.nama, bank.logo)"
          v-for="bank in payment.bank"
          :key="bank.nama"
        >
          <b-media vertical-align="end">
            <template v-slot:aside>
              <b-img
                left
                :src="require(`@/assets/payment/${bank.logo}`)"
                blank-color="#ccc"
                width="64"
                alt="placeholder"
              ></b-img>
            </template>
            <h6 class="mt-1 align-middle">
              {{ bank.nama }}
              <span v-if="bank.nama === paymentSelected.nama">
                <b-icon-check
                  variant="primary"
                  class="float-right"
                  scale="2"
                ></b-icon-check>
              </span>
            </h6>
          </b-media>
        </b-list-group-item>
      </b-list-group>
    </b-card>

    <b-card no-body>
      <template v-slot:header>
        <h6 class="m-0 p-0">
          <strong>Pembayaran via Kartu Kredit</strong>
        </h6>
      </template>
      <b-list-group flush>
        <b-list-group-item
          class="list-item"
          @click="setPayment(kredit.nama, kredit.logo)"
          v-for="kredit in payment.kredit"
          :key="kredit.nama"
        >
          <b-media vertical-align="end">
            <template v-slot:aside>
              <b-img
                left
                :src="require(`@/assets/payment/${kredit.logo}`)"
                blank-color="#ccc"
                width="64"
                height="40"
                alt="placeholder"
              ></b-img>
            </template>
            <h6 class="mt-2 align-middle">
              {{ kredit.nama }}
              <span v-if="kredit.nama === paymentSelected.nama">
                <b-icon-check
                  variant="primary"
                  class="float-right"
                  scale="2"
                ></b-icon-check>
              </span>
            </h6>
          </b-media>
        </b-list-group-item>
      </b-list-group>
    </b-card>
  </div>
</template>

<script>
export default {
  name: "Payment",
  props: {
    nominal: {
      type: Number
    }
  },
  data() {
    return {
      paymentSelected: {
        nama: null,
        logo: null
      },
      payment: {
        kilat: [
          {
            nama: "GO-PAY",
            logo: "go-pay.png"
          },
          {
            nama: "LinkAja",
            logo: "linkaja.png"
          },
          {
            nama: "OVO",
            logo: "ovo.png"
          },
          {
            nama: "DANA",
            logo: "dana.png"
          }
        ],
        bank: [
          {
            nama: "BCA",
            logo: "bca.png"
          },
          {
            nama: "BNI",
            logo: "bni.png"
          },
          {
            nama: "MANDIRI",
            logo: "mandiri.png"
          },
          {
            nama: "BNI SYARIAH",
            logo: "bni-syariah.png"
          }
        ],
        kredit: [
          {
            nama: "MASTERCARD",
            logo: "mastercard.png"
          },
          {
            nama: "VISA",
            logo: "visa.png"
          }
        ]
      }
    };
  },
  methods: {
    setPayment(nama, logo) {
      this.paymentSelected.nama = nama;
      this.paymentSelected.logo = logo;
      this.$emit("select-payment", this.paymentSelected);
    }
  }
};
</script>

<style lang="scss" scoped>
.list-item {
  cursor: pointer;
}
</style>
