<template>
  <div id="jumbotron" class="mx-auto">
    <transition name="expand">
      <keep-alive>
        <components
          :is="showComponent"
          @pembayaran-donasi="getData"
          :nominal="getNominal"
          @select-payment="setPayment"
          @ganti-pembayaran="gantiPembayaran"
          :payment="payment"
          @back="back"
          :nominalProps="getNominal"
        ></components>
      </keep-alive>
    </transition>
  </div>
</template>

<script>
import DonasiVue from "../components/Donasi.vue";
import PaymentVue from "../components/Payment.vue";
import FinalDonasi from "../components/FinalDonasi.vue";
export default {
  name: "Donasi",
  components: {
    "donasi-component": DonasiVue,
    "payment-component": PaymentVue,
    "final-component": FinalDonasi
  },
  data() {
    return {
      showComponent: "donasi-component",
      getNominal: null,
      payment: {
        nama: null,
        logo: null
      }
    };
  },
  methods: {
    getData(val) {
      this.getNominal = parseInt(val);
      this.showComponent = "payment-component";
    },
    gantiPembayaran() {
      this.showComponent = "payment-component";
    },
    setPayment(val) {
      this.payment = val;
      this.showComponent = "final-component";
    },
    back(val) {
      this.showComponent = val;
    }
  }
};
</script>

<style lang="scss" scoped></style>
