<template>
  <div>
    <div class="back pl-3">
      <b-button to="/" id="btn-back">
        <b-icon icon="arrow-left-short" variant="light" scale="2"></b-icon>
      </b-button>
    </div>
    <b-card tag="article" class="shadow pb-5">
      <h5 class="text-info text-center py-3">
        <strong>Masukan Nominal Donasi</strong>
      </h5>
      <b-img-lazy
        v-bind="mainProps"
        src="../assets/donasi.png"
        alt="Image 1"
      ></b-img-lazy>
      <b-card class="my-2 nominal" @click="sendNominal(10000)" no-body>
        <b-media class="py-2 px-3" right-align vertical-align="center">
          <template v-slot:aside>
            <b-img
              src="../assets/arrow.svg"
              blank-color="#ccc"
              width="25"
              alt="placeholder"
            ></b-img>
          </template>
          <h6 class="mt-0">
            <strong>Rp 10.000</strong>
          </h6>
          <p class="mb-0 bg-light py-1 px-2 notif">
            <small>Nominal Minimal Donasi</small>
          </p>
        </b-media>
      </b-card>
      <b-card class="my-2 nominal" @click="sendNominal(20000)" no-body>
        <b-media class="py-2 px-3 pt-3" right-align vertical-align="center">
          <template v-slot:aside>
            <b-img
              src="../assets/arrow.svg"
              blank-color="#ccc"
              width="25"
              alt="placeholder"
            ></b-img>
          </template>
          <h6 class="mt-0">
            <strong>Rp 20.000</strong>
          </h6>
        </b-media>
      </b-card>
      <b-card class="my-2 nominal" @click="sendNominal(50000)" no-body>
        <b-media class="py-2 px-3 pt-3" right-align vertical-align="center">
          <template v-slot:aside>
            <b-img
              src="../assets/arrow.svg"
              blank-color="#ccc"
              width="25"
              alt="placeholder"
            ></b-img>
          </template>
          <h6 class="mt-0">
            <strong>Rp 50.000</strong>
          </h6>
        </b-media>
      </b-card>
      <b-card class="my-2 nominal" @click="sendNominal(100000)" no-body>
        <b-media class="py-2 px-3" right-align vertical-align="center">
          <template v-slot:aside>
            <b-img
              src="../assets/arrow.svg"
              blank-color="#ccc"
              width="25"
              alt="placeholder"
            ></b-img>
          </template>
          <h6 class="mt-0">
            <strong>Rp 100.000</strong>
          </h6>
          <p class="mb-0 bg-light py-1 px-2 notif">
            <small>Nominal yang sering dipilih</small>
          </p>
        </b-media>
      </b-card>
      <b-card class="my-3 nominal" no-body>
        <b-media class="py-2 px-3" right-align vertical-align="center">
          <h6 class="mt-0">
            <strong>Nominal Donasi Lainnya</strong>
          </h6>
          <div class>
            <b-input-group size="md" right prepend="Rp">
              <b-form-input
                v-model="nominalDonasi"
                :state="validation"
              ></b-form-input>
            </b-input-group>
          </div>
        </b-media>
      </b-card>
    </b-card>
    <b-navbar id="btn-container" fixed="bottom">
      <b-button
        block
        @click="sendNominal(nominalDonasi)"
        variant="danger"
        center
        id="btn-pembayaran"
        :disabled="!validation"
      >
        <strong>LANJUTKAN PEMBAYARAN</strong>
      </b-button>
    </b-navbar>
  </div>
</template>
<script>
export default {
  name: "Donasi",
  data() {
    return {
      mainProps: {
        center: true,
        fluidGrow: true,
        blank: true,
        blankColor: "#bbb",
        width: 600,
        height: 400
      },
      nominalDonasi: 0
    };
  },
  methods: {
    sendNominal(val) {
      this.nominalDonasi = val;
      this.$emit("pembayaran-donasi", this.nominalDonasi);
    }
  },
  computed: {
    validation() {
      return (
        this.nominalDonasi.length > 4 &&
        !isNaN(this.nominalDonasi) &&
        this.nominalDonasi.length < 9 &&
        parseInt(this.nominalDonasi) / 10000 !== 0 &&
        Number.isInteger(parseInt(this.nominalDonasi) / 10000)
      );
    }
  }
};
</script>

<style lang="scss">
.nominal {
  cursor: pointer;
  &:hover {
    border-color: RGB(0, 173, 239);
    .notif {
      background: rgb(200, 227, 255) !important;
    }
  }
}
#btn-container {
  max-width: 500px;
  margin: 0 auto;
  background: rgb(235, 233, 233);
  & #btn-pembayaran {
    min-width: 350px;
    margin: 0 auto;
  }
}
.back {
  position: fixed;
  top: 13px;
  z-index: 99999;
  cursor: pointer;
}
#btn-back {
  background: transparent;
  padding: 0;
  margin: 0;
  border: none;
  &:focus {
    box-shadow: none;
  }
}
</style>
